"use strict"
/// –ø–µ—Ä–µ–∏–º–∏–Ω–æ–≤–∞—Ç—å —Ñ–∞–π–ª –≤ –º–µ–Ω—é
class Menu {
    constructor(data) {
        this._init()
        // this.data = data

        this._getPr(data)
    }

    clickIconMenu(e) {
        e.target.classList.remove('fa-times')
        e.target.classList.add('fa-bars')
        this.menuNav.classList.remove('menu-show')
        document.body.style.overflow = ""

    }
    clickIconMenuClose(e) {
        e.target.classList.add('fa-times')
        e.target.classList.remove('fa-bars')
        this.menuNav.classList.add('menu-show')
        document.body.style.overflow = "hidden"
    }

    _getPr(data) {
        this.data = data
        let a = this.data
        this.b = a.map(item => {
            return `<li><a class="menu__link" href="./index.html">${item.text}</a></li>`
        })

    }
    _getMenu() { //—Å–¥–µ–ª–∞—Ç—å –º–æ–¥—É–ª–∏
        return `
            <a href="index.html" class="menu__logo"><span>Anzhelika Kusova</span></a>
            <div class="menu__wrapp">
                <div class="menu__icon">
                    <i class="fas fa-bars" data-js="menu"></i>
                </div>
                <nav class="menu__nav">
                    <ul class="menu__list">
                        <li><a class="menu__link" href="./index.html"></a></li>
                        <li><a class="menu__link" href="portfolio.html">–ü–æ—Ä—Ç—Ñ–æ–ª–∏–æ</a></li>
                        <li><a class="menu__link" href="price.html">–ü—Ä–∞–π—Å</a></li>
                        <li><a class="menu__link" href="contacts.html">–ö–æ–Ω—Ç–∞–∫—Ç—ã</a></li>
                    </ul>
                </nav>
            </div>
        `
    }
    _handlerClickBtn(e) {
        e.target.classList.contains("fa-times") ? this.clickIconMenu(e) : this.clickIconMenuClose(e)
    }
    _render() {
        this.menu = document.querySelector('#menu')
        this.menu.innerHTML = this._getMenu()
    }
    _addEvents() {
        this._handlerClickBtn = this._handlerClickBtn.bind(this)
        this.menuNav = document.querySelector('.menu__nav') /// –≤—Ä–µ–º–µ–Ω–Ω–æ–µ –º–µ—Å—Ç–æ –¥–µ—Å–ª–∞–∫–∞—Ü–∏–∏
        this.iconMenu = document.querySelector('[data-js="menu"]')
        this.iconMenu.addEventListener('click', this._handlerClickBtn)
    }
    _init() {
        this._render()
        this._addEvents()
    }


}
export default Menu



